import {constants} from "./constants";

export function initLogosSlider() {

	const $logosSlider = $('.logos .slider');

	$logosSlider.slick({
		dots: false,
		arrows: false,
		infinite: true,
		speed: 300,
		slidesToShow: 5,
		swipeToSlide: true,
		slidesToScroll: 1,
		autoplay: !constants.IExplorer,
		autoplaySpeed: 2000,
		responsive: [
			{
				breakpoint: 1800,
				settings: {
					slidesToShow: 4,
					slidesToScroll: 1
				}
			},
			{
				breakpoint: 768,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 1
				}
			}
		]
	});

	$('.logos .slider-arrow-left').click(function(){
		$logosSlider.slick('slickPrev');
	});

	$('.logos .slider-arrow-right').click(function(){
		$logosSlider.slick('slickNext');
	});
}


